
.calculator-advanced {
    widtH: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: white;
}

.advanced-left-column, .advanced-middle-column, .advanced-right-column {
    display: flex;
    flex-direction: column;
    width: 33%;
    height: 100%;
    overflow-y: auto;
}

.advanced-left-column>table {
    display: table;
    table-layout: auto;
    border-collapse: collapse;
    width: 100%;
}

.advanced-left-column>table>tbody>tr>td {
    border: 1px solid black;
    text-align: center;
    padding: 5px;
    background-color: rgba(20, 20, 20, .2);
}

.advanced-left-column>table>tbody>tr>th {
    border: 1px solid black;
    text-align: center;
    width: 100%;
    padding: 5px;
}

.table-second-holder {
    width: 100%;
    margin-top: 10px;
    height: 46%;
}

.advanced-middle-column>table, .advanced-right-column>table {
    display: table;
    flex-direction: column;
    border-collapse: collapse;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}

.advanced-middle-column>table>tbody, .advanced-right-column>table>tbody {
    width: 100%;
    height: 100%;
}

.advanced-middle-column>table>tbody>tr, .advanced-right-column>table>tbody>tr, .advanced-left-column>table>tbody>tr {
    display: flex;
    width: 100%;
}

.advanced-middle-column>table>tbody>tr>td, .advanced-right-column>table>tbody>tr>td, .advanced-left-column>table>tbody>tr>td {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-right: none;
    padding: 5px;
    width: 100%;
    text-align: center;
    background-color: rgba(20, 20, 20, .2);
}

.advanced-left-column>table>tbody>tr>td {
    border: 1px solid black;
}

.title-cell {
    border: 1px solid black;
    text-align: center;
    width: 100%;
    padding: 10px;
}




@media (min-width:641px) and (max-width: 1201px) {
    .advanced-middle-column>table>tbody>tr>td, .advanced-right-column>table>tbody>tr>td, .advanced-left-column>table>tbody>tr>td {
        padding: 2px;
    }
    .calculator-advanced {
        flex-wrap: wrap;
    }
}

@media (min-width: 320px) and (max-width: 640px) {
    .calculator-advanced {
        flex-direction: column;
    }
    .advanced-left-column, .advanced-middle-column, .advanced-right-column {
        width: 100%;
    }
    .calculator-advanced>div>h3 {
        margin-top: 20px
    }

}